.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: var(--trans-effect);
    overflow: scroll;
}

.backdrop.is-hidden {
    pointer-events: none;
    visibility: hidden;
}

.ingrediens {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 280px;
    height: 990px;
    padding: 0 20px;
    background: var(--modal-bg);
    border-radius: 10px;
    border: 2px solid var(--accent-color);

    @media screen and (min-width: 768px){
    width: 568px;
    height: 995px;
    padding: 0 40px;
    }

}

.ingrediens__list{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 9px;

    font-weight: 500;
        font-size: 10px;
        line-height: calc(28/10);
        color: var(--primary-text-color);

        @media screen and (min-width: 768px) {
                gap: 20px;
            }

}

.ingrediens__title{
    margin-bottom: 22px;
}

.ingrediens__elipse{
    width: 132px;
    height: 130.6px;
    border-radius: 50%;
   background: linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)), #D9D9D9;
    box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);
    margin-bottom: 24px;
}

.ingrediens__img{
    position: relative;
    bottom:  8px;
}

.ingrediens__list{
    margin-bottom: 39px;
}

.ingrediens__text{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 53px;
    height: 72px;
    border: 2px solid var(--composition-main-color);
    border-radius: 40px;

    @media screen and (min-width: 768px){
        width: 65px;
         height: 90px;
    }


}
.ingrediens_desk{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 43px;
        height: 43px;
        background: var(--composition-main-color);
        box-shadow: 0px 6px 20px rgba(10, 136, 23, 0.25);
        border-radius: 50px;
          

        @media screen and (min-width: 768px) {
                width: 57px;
                height: 57px;

            }
       
}

.ingrediens__litle--group{
    display: flex;
    flex-direction: column;
    margin-bottom: 24px;
  
}
.ingrediens__list--croup{
    display: flex;
    flex-direction: column;

    font-weight: 500;
        font-size: 20px;
        line-height: calc(28/20);
        color: var(--primary-text-color);

       width: 240px;
    margin-bottom: 48px;

        @media screen and (min-width: 768px) {
            width: 488px;
            
        
            }

}

.ingrediens__text--group{
    display: flex;
    justify-content: space-between;


    font-size: 14px;
    line-height: calc(25/14);
    letter-spacing: 0.04em;
    color: var(--primary-text-color);
    margin-bottom: 11px;

}

.ingrediens__label{
    @media screen and (min-width: 768px) {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
}

.ingrediens__input {
    width: 100%;
    height: 42px; 
    outline: transparent;
    background: rgba(248, 210, 78, 0.1);
        border: 1px solid var(--main-color);
        border-radius: 10px;
        margin-bottom: 16px;
        padding: 0 10px;


        @media screen and (min-width: 768px) {
            width: 368px;
            }
        
   
}

.ingrediens__input::placeholder {
    font-size: 14px;
    line-height: calc(20/14);
    color: var(--primary-text-color);
}

.ingrediens__textarea {
    width: 100%;
    height: 84px;
    resize: none;
    outline: transparent;
background: rgba(248, 210, 78, 0.1);
    border: 1px solid var(--main-color);
    border-radius: 10px;
    transition: border var(--animation);
    padding: 0 10px;
    padding-top: 11px;

    @media screen and (min-width: 768px) {
            width: 368px;
        }

}


.ingrediens__textarea::placeholder{
    font-size: 14px;
    line-height: calc(20/14);
    color: var(--primary-text-color);
}

.ingrediens__form{
    margin-bottom: 32px;

    @media screen and (min-width: 768px) {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
}

.ingrediens__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: inherit;

    font-weight: 600;
    font-size: 14px;
    line-height: calc(17/14);

    width: 132px;
    min-height: 42px;
    padding: 12px 34px;

    cursor: pointer;
    border: none;
    color: var(--primary-text-color);
    background-color: var(--accent-color);
    border-radius: 10px;

   
   
}