.backdrop-franchise{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    padding: 93px 15px;
    background-color:rgba(0, 0, 0, 0.2);
    overflow-y: scroll;
    opacity: 1;
    visibility: visible;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &.is-hidden{
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
}

.modal-franchise{
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: center;
    align-content: center;
    width: 100%;
    max-width: 280px;
    padding: 28px 30px;
    background-color: var(--primary-white-color);
    border: 2px solid var(--accent-color);
    border-radius: 10px;
    transform: translate(-50%,-50%) scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    
    @media screen and (min-width: 1200px){
        // max-width: 568px;
    }
    
    &__title{
    margin-top: 0;
    margin-bottom: 20px;
    text-align: center;
    color: var( --primary-text-color);
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
}
    &__text{
        width: 222px;
        margin-top: 0;
        margin-bottom: 20px;
        color: var( --primary-text-color);
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
    }

    .backdrop-franchise.is-hidden &{
    transform: translate(-50%,-50%) scale(0.9);
}
}
.btn-modal__icon{
    width: 100%;
    height: 100%;
    fill: var(--primary-text-color);
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    .modal__close:hover &,
    .modal__close:focus &{
    fill: var(--accent-color);
}
}

.form-franchise{
    width: 100%;
    height: 100%;
    text-align: left;
}

.form__field{
    position: relative;
   margin-bottom: 12px;
   display: flex;
   flex-direction: column;
    &:nth-child(7){
    margin-bottom: 24px;
    }
}

.form__input{
    width: 100%;
    height: 32px;
    padding-left: 12px;
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    &::placeholder{
        color: var(--primary-text-color);
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
    }
    &:hover,
    &:focus{
    border: 1px solid var(--accent-color);
}
}

.textarea{
    display: block;
    height: 64px;
    padding: 12px;
    resize: none;
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;    
       

    &::placeholder{
    color: var(--primary-text-color);
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
}
    &:hover, 
    &:focus{
        border: 1px solid var(--accent-color);
    }
}

.btn-submit{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 114px;
    height: 42px;
    padding: 0;
    margin-left: auto;
    margin-right: auto;
    color: var(--primary-text-color) ;
    background-color: var(--accent-color);
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    border-color: transparent;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    cursor: pointer;
}