.location__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);

  opacity: 1;

  &.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
      opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
      visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  &.is-hidden .locations {
    transform: translate(-50%, -50%) scale(0.5);
  }
}

.locations {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  //   display: flex;
  //   flex-direction: column;
  //   align-items: center;
  //   justify-content: center;

  // max-height: 365px;
  max-width: 280px;
  padding: 28px;
  background: var(--modal-bg);
  border-radius: 10px;
  border: 2px solid #f8d24e;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.locations__title {
  text-align: center;
  margin-bottom: 20px;
}

.location-wrapper__iframe {
  margin-bottom: 24px;
  border-radius: 10px;
}

.location__iframe {
  width: 220px;
}

.locations__button {
  font-family: inherit;

  cursor: pointer;
  display: block;
  margin: 0 auto;
  padding: 12px 34px;

  background-color: var(--accent-color);
  border-radius: 10px;
  border: none;
}

.locations__link {
  font-weight: 600;
  font-size: 12px;
  line-height: calc(15 / 12);
  color: var(--primary-text-color);
}
